.city-page {
  padding-top: 80px;
  padding-bottom: 40px;
  min-height: 100vh;
  background: #282c34;
}

.city-page.loading,
.city-page.error {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #888;
  font-size: 1.2rem;
}

.city-page.error {
  color: #ff4d4f;
}

.city-header {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px 30px;
}

.city-header h1 {
  color: #61dafb;
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.view-controls {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.view-controls button {
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid #444;
  border-radius: 4px;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.2s;
}

.view-controls button:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: #61dafb;
}

.view-controls button.active {
  background: rgba(97, 218, 251, 0.2);
  border-color: #61dafb;
  color: #61dafb;
}

.chart-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
}

.stats-table {
  max-width: 1400px;
  margin: 30px auto 0;
  padding: 0 20px;
}

.stats-table h2 {
  color: #fff;
  font-size: 1.5rem;
  margin-bottom: 15px;
}

.stats-table table {
  width: 100%;
  border-collapse: collapse;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid #444;
  border-radius: 4px;
  overflow: hidden;
}

.stats-table tbody tr {
  border-bottom: 1px solid #444;
}

.stats-table tbody tr:last-child {
  border-bottom: none;
}

.stats-table td {
  padding: 12px 16px;
  color: #aaa;
}

.stats-table td:first-child {
  font-weight: 600;
  color: #fff;
  width: 200px;
}

.stats-table td:last-child {
  color: #61dafb;
}
