.landing-page {
  padding-top: 80px;
  min-height: 100vh;
  background: #282c34;
}

.landing-header {
  text-align: center;
  margin-bottom: 40px;
  padding: 0 20px;
}

.landing-header h1 {
  color: #61dafb;
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.subtitle {
  color: #888;
  font-size: 1.1rem;
  margin-bottom: 30px;
}

.search-bar {
  max-width: 500px;
  margin: 0 auto;
}

.search-bar input {
  width: 100%;
  padding: 12px 20px;
  font-size: 16px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid #444;
  border-radius: 8px;
  color: #fff;
  outline: none;
  transition: all 0.2s;
}

.search-bar input:focus {
  border-color: #61dafb;
  background: rgba(255, 255, 255, 0.08);
}

.search-bar input::placeholder {
  color: #666;
}

.loading {
  text-align: center;
  color: #888;
  font-size: 1.2rem;
  padding: 100px 20px;
}

.city-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
  padding: 0 20px 40px;
  max-width: 1400px;
  margin: 0 auto;
}

.city-card {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid #444;
  border-radius: 8px;
  padding: 20px;
  text-decoration: none;
  color: inherit;
  transition: all 0.2s;
  cursor: pointer;
}

.city-card:hover {
  background: rgba(255, 255, 255, 0.08);
  border-color: #61dafb;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(97, 218, 251, 0.1);
}

.city-card.both_resolved {
  border-color: #ff4d4f;
  background: rgba(255, 77, 79, 0.05);
}

.city-card.both_resolved:hover {
  border-color: #ff7875;
  background: rgba(255, 77, 79, 0.1);
}

.city-card.active_low {
  border-color: #4caf50;
  background: rgba(76, 175, 80, 0.05);
}

.city-card.active_low:hover {
  border-color: #66bb6a;
  background: rgba(76, 175, 80, 0.1);
}

.city-card.active {
  border-color: #555;
  background: rgba(255, 255, 255, 0.03);
}

.city-card.active:hover {
  border-color: #666;
  background: rgba(255, 255, 255, 0.05);
}

.city-name {
  font-size: 1.3rem;
  font-weight: 600;
  color: #fff;
  margin-bottom: 8px;
}

.city-status {
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 15px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.probabilities {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.prob-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.95rem;
}

.prob-label {
  font-weight: 600;
}

.prob-value {
  color: #aaa;
}
