.main-navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  z-index: 1000;
  border-bottom: 1px solid #333;
}

.nav-left {
  display: flex;
  gap: 20px;
  align-items: center;
}

.nav-link {
  color: #888;
  text-decoration: none;
  padding: 8px 16px;
  border-radius: 4px;
  transition: all 0.2s;
  font-weight: 500;
}

.nav-link:hover {
  color: #fff;
  background: rgba(255, 255, 255, 0.1);
}

.nav-link.active {
  color: #61dafb;
  background: rgba(97, 218, 251, 0.1);
}

.city-selector {
  background: rgba(255, 255, 255, 0.05);
  color: #888;
  border: 1px solid #555;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.2s;
  outline: none;
}

.city-selector:hover {
  background: rgba(255, 255, 255, 0.1);
  border-color: #777;
  color: #fff;
}

.city-selector:focus {
  border-color: #61dafb;
  color: #fff;
}

.city-selector option {
  background-color: #222;
  color: #fff;
}

.nav-right {
  display: flex;
  align-items: center;
  gap: 15px;
}

.user-info {
  color: #aaa;
  font-size: 14px;
}

.logout-btn {
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  border: 1px solid #555;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.2s;
}

.logout-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: #777;
}
